@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #000;
  color: #fff;
  font-family: 'Anton', sans-serif;
  min-height: 100vh;
  position: relative;
}
/* MAIN CONTAINER */
.navbar__container {
    height: 10vh;
    background-color: #fff;
    color: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 1rem;
    font-size: 1.5rem;
}
.navbar__logo {
    text-transform: uppercase;
}
/* LINKS CONTAINER */
.navbar__links--container {
    display: flex;
}
/* SIDEBAR OPEN/CLOSE BUTTONS */
.sidebar__toggle--btn {
    display: none;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    transition: 0.3s;
}
.sidebar__toggle--btn:hover {
    transform: scale(1.1);
}
/* RESPONSIVE NAVBAR */
@media screen and (max-width: 500px){
    .navbar__links--container {
        display: none;
    }
    .sidebar__toggle--btn {
        display: block;
    }
}
.navbar__link {
    font-size: 1.2rem;
    margin-right: 0.5rem;
    cursor: pointer;
    text-transform: uppercase;
}
.navbar__link:hover, .navbar__link:active{
    transform: scale(1.1);
}
.hidden {
    display: none;
}
.hero__container {
  height: 90vh;
  background: url(/static/media/hero.8dabd5b4.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  align-items: center;
}
.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.453);
  z-index: 1;
}
.hero__titles--container {
  z-index: 2;
  padding: 2rem;
}
.hero__main--title {
  font-size: 5rem;
  color: #000;
  text-transform: uppercase;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}
.hero__sub--title {
  font-size: 2.5rem;
  color: #000;
  text-transform: uppercase;
  text-align: right;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}
.hero__info--text {
  margin-top: 2rem;
  text-align: left;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.953);
  letter-spacing: 1px;
}

.sidebar__container {
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 0;
  z-index: 30;
  background-color: #000;
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
}
.sidebar__top--container {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-size: 1.5rem;
}
.sidebar__logo {
  text-transform: uppercase;
}
.close-sidebar__btn {
  transition: 0.3s;
  cursor: pointer;
}
.close-sidebar__btn:hover {
  transform: scale(1.1);
}
.sidebar__links--container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 2rem 0;
}
.sidebar--link {
    cursor: pointer;
    padding: 1rem 1rem;
}
.sidebar--link:hover {
    background-color: rgb(255, 255, 255);
    color: #000;
}
.hidden {
    display: none;
    width: 0%;
}
